.task-item {
    background-color: #efe9e9;
    border: 1px solid #cccccc;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
  }
  
  .task-item:hover {
    background-color: #ebe5e5;
  }
  
  .task-details {
    flex-grow: 1;
  }
  
  .edit-input {
    margin-right: 10px;
    padding: 5px;
    border: 1px solid #cccccc;
    border-radius: 3px;
  }
  
  .edit-button, .delete-button {
    margin-left: 10px;
    padding: 5px 10px;
    border: none;
    border-radius: 3px;
    cursor: pointer;
  }
  
  .edit-button:hover, .delete-button:hover {
    background-color: #24676d;
  }
  