.task-list-container {
    background-color: #b2ceea;
    border: 1px solid #dee2e6;
    border-radius: 5px;
    padding: 20px;
    margin-top: 20px;
  }
  
  .task-list {
    list-style-type: none;
    padding: 0;
  }
  
  .task-list li {
    background-color: rgb(189, 194, 197);
    border: 1px solid #ced4da;
    border-radius: 5px;
    margin-bottom: 10px;
    padding: 10px;
    transition: background-color 0.3s ease;
  }
  
  .task-list li:hover {
    background-color: rgb(77, 162, 205);
  }
  
  .filter-sort {
    margin-bottom: 10px;
  }
  
  .filter-sort label {
    margin-right: 5px;
  }
  